          {row: 42, col: 9, colspan: 9, rowspan: 1},
          {row: 42, col: 18, colspan: 2, rowspan: 1},
          {row: 48, col: 2, colspan: 6, rowspan: 1},
          {row: 48, col: 9, colspan: 9, rowspan: 1},
          {row: 48, col: 18, colspan: 2, rowspan: 1},
          {row: 54, col: 2, colspan: 6, rowspan: 1},
          {row: 54, col: 9, colspan: 9, rowspan: 1},
          {row: 54, col: 18, colspan: 2, rowspan: 1},
          {row: 60, col: 2, colspan: 6, rowspan: 1},
          {row: 60, col: 9, colspan: 9, rowspan: 1},
          {row: 60, col: 18, colspan: 2, rowspan: 1}
        ],
        minRows: 70, // 最小行列
        minCols: 26,
        cells: function (row, col, prop) { // 设置单元格只读
          var cellProperties = {}
          if (col === 0 || col === 1 || row === 0 || row === 2 || row === 19 ||
          row === 20 || row === 21 || row === 24 || row === 25 || row === 30 ||
          row === 31 || row === 36 || row === 37 || row === 42 || row === 43 ||
          row === 48 || row === 49 || row === 54 || row === 55 || row === 60 ||
          row === 61) {
            cellProperties.readOnly = true
          }
          if ((row === 1 && col === 11) || (row === 1 && col === 12) || (row === 1 && col === 13) ||
            (row === 1 && col === 14) || (row === 1 && col === 15) || (row === 1 && col === 16) ||
            (row === 2 && col === 11) || (row === 3 && col === 11) || (row === 4 && col === 11) ||
            (row === 5 && col === 11) || (row === 6 && col === 11) || (row === 7 && col === 11) ||
            (row === 8 && col === 11) || (row === 9 && col === 11) || (row === 10 && col === 11)
          ) {
            cellProperties.readOnly = true
          }
          return cellProperties
        },
        colHeaders: true,
        rowHeaders: true
      },
      t3t4data: t3t4data.settings2,
      newflag: true,
      t1: null
    }
  },
  mounted: function () {
    var _this = this
    // 第一次加载获取历史数据
    _this.loadingTip = '正在加载历史数据'
    getsheet(null, null).then((res) => {
      // 保存数据
      grobalData.t0 = res.data.t0
      grobalData.t3_t4 = res.data.t3_t4
      _this.setT0AllData(res.data.t0)
      // t3_t4
      console.log(_this.$refs.t3_t4.table)
      this.loading = false
    })
  },
  methods: {
    setSheetData: function (sheet, data, start, end) {
      for (var i = 0; i < data.length; i++) {
        for (var j = 0; j < data[i].length; j++) {
          sheet.setDataAtCell(start + i, j + end, data[i][j])
        }
      }
    },
    setT0AllData: function (t0) {
      var _this = this
      this.loading = true
      // 设置第一个表
      _this.setSheetData(_this.$refs.t0.table, t0.register, 1, 2)
      // 设置市场信息表
      _this.setSheetData(_this.$refs.t0.table, t0.market, 3, 12)
      // 存款业务信息登记
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[0], 22, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[1], 26, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[2], 32, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[3], 38, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[4], 44, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[5], 50, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[6], 56, 2)
      _this.setSheetData(_this.$refs.t0.table, t0.repositService[7], 62, 2)
      // 贷款业务信息登记表
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[0], 22, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[1], 26, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[2], 32, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[3], 38, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[4], 44, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[5], 50, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[6], 56, 9)
      _this.setSheetData(_this.$refs.t0.table, t0.loadService[7], 62, 9)
      this.loading = false
    },
    setT34AllData: function (t3t4) {
      var _this = this
      // T3:国债购买记录
      _this.setSheetData(_this.$refs.t3_t4.table, t3t4.nationalDebtPurchase, 2, 2)
      // T3:国债交易记录表
      _this.setSheetData(_this.$refs.t3_t4.table, t3t4.nationalDebtTrade, 14, 1)
      // T5
      _this.setSheetData(_this.$refs.t3_t4.table, t3t4.interBank, 3, 15)
      _this.setSheetData(_this.$refs.t3_t4.table, t3t4.outBank, 17, 15)
      // T4
      _this.setSheetData(_this.$refs.t3_t4.table, t3t4.investProfit, 2, 24)
      _this.loading = false
    },
    calculate: function () {
      var _this = this
      _this.loadindTip = '正在提交计算,耐心等候'
      console.log('提交进行计算')
      var register = this.$refs.t0.table.getData(1, 2, 15, 9)
      var market = this.$refs.t0.table.getData(3, 12, 10, 16)
      var repositService = new Array(8)
      var loadService = new Array(8)
      // t0数据
      repositService[0] = this.$refs.t0.table.getData(22, 2, 23, 7)
      repositService[1] = this.$refs.t0.table.getData(26, 2, 29, 7)
      repositService[2] = this.$refs.t0.table.getData(32, 2, 35, 7)
      repositService[3] = this.$refs.t0.table.getData(38, 2, 41, 7)
      repositService[4] = this.$refs.t0.table.getData(44, 2, 47, 7)
      repositService[5] = this.$refs.t0.table.getData(50, 2, 53, 7)
      repositService[6] = this.$refs.t0.table.getData(56, 2, 59, 7)
      repositService[7] = this.$refs.t0.table.getData(62, 2, 66, 7)
      loadService[0] = this.$refs.t0.table.getData(22, 9, 23, 20)
      loadService[1] = this.$refs.t0.table.getData(26, 9, 29, 20)
      loadService[2] = this.$refs.t0.table.getData(32, 9, 35, 20)
      loadService[3] = this.$refs.t0.table.getData(38, 9, 41, 20)
      loadService[4] = this.$refs.t0.table.getData(44, 9, 47, 20)
      loadService[5] = this.$refs.t0.table.getData(50, 9, 53, 20)
      loadService[6] = this.$refs.t0.table.getData(56, 9, 59, 20)
      loadService[7] = this.$refs.t0.table.getData(62, 9, 66, 20)
      // t3_t4数据
      var nationalDebtPurchase = this.$refs.t3_t4.table.getData(2, 2, 9, 13)
      var nationalDebtTrade = this.$refs.t3_t4.table.getData(14, 1, 21, 14)
      var interBank = this.$refs.t3_t4.table.getData(3, 15, 14, 21)
      var outBank = this.$refs.t3_t4.table.getData(17, 15, 28, 21)
      var investProfit = this.$refs.t3_t4.table.getData(2, 24, 9, 27)
      console.log(nationalDebtPurchase)
      console.log(nationalDebtTrade)
      console.log(interBank)
      console.log(outBank)
      console.log(investProfit)
      var t0 = {
        loadService: loadService,
        market: market,
        register: register,
        repositService: repositService
      }
      var t3t4 = {
        nationalDebtPurchase: nationalDebtPurchase,
        nationalDebtTrade: nationalDebtTrade,
        interBank: interBank,
        outBank: outBank,
        investProfit: investProfit
      }
      console.log(t3t4)
      getsheet(t0, t3t4).then((res) => {
        if (res.data.success === 1) {
          grobalData.t0 = res.data.t0
          grobalData.t3_t4 = res.data.t3_t4
          _this.setT0AllData(res.data.t0)
          _this.setT34AllData(res.data.t3_t4)
        } else { // 清除登录态
          sessionStorage.clear()
          console.log('登录超时')
          _this.$router.push({
            name: 'login'
          })
        }
      })
    },
    // tapclick: function (targerName) {
    //   var _this = this
    //   // _this.$refs.t3_t4.table.setDataAtCell(3, 21, '第一期')
    //   console.log(targerName)
    //   var id = targerName.$options.propsData.name
    //   if (id === 'first') {
