    //     console.log('firstxxx')
    //   } else if (id === 'second') {
    //     // 点击第二个标签时
    //     console.log('secondclick')
    //     _this.$refs.t3_t4.table.render()
    //   }
    // },
    beforeleave: function (activeName, oldActiveName) {
      var _this = this
      console.log(_this.newflag)
      if (activeName === 'second' && oldActiveName === 'first' && this.newflag) {
        // 点击第二个标签 且是第一次加载
        _this.loading = true
        setTimeout(function () {
          _this.$refs.t3_t4.table.render()
          console.log('xuanr')
        }, 100)
        console.log(activeName)
        console.log(oldActiveName)
        // T3:国债购买记录
        _this.setSheetData(_this.$refs.t3_t4.table, grobalData.t3_t4.nationalDebtPurchase, 2, 2)
        // T3:国债交易记录表
        _this.setSheetData(_this.$refs.t3_t4.table, grobalData.t3_t4.nationalDebtTrade, 14, 1)
        // T5
        _this.setSheetData(_this.$refs.t3_t4.table, grobalData.t3_t4.interBank, 3, 15)
        _this.setSheetData(_this.$refs.t3_t4.table, grobalData.t3_t4.outBank, 17, 15)
        // T4
        _this.setSheetData(_this.$refs.t3_t4.table, grobalData.t3_t4.investProfit, 2, 24)
        _this.loading = false
      } else if (activeName === 'first' && oldActiveName === 'second') {
        // 点击第一个标签
        // _this.$refs.t3_t4.table.render()
      } else {
        _this.loadingTip = '正在加载数据'
        if (activeName === 'third') {
          _this.loading = true
          getsheetHtml('T1存款利息表').then((res) => {
            if (res.data.success === 1) {
              console.log(res.data)
              _this.t1 = res.data.sheet
              _this.loading = false
            } else { // 清除登录态
              sessionStorage.clear()
              console.log('登录超时')
              _this.$router.push({
                name: 'login'
              })
            }
          })
        } else if (activeName === 'fouth') {
          _this.loading = true
          getsheetHtml('T2贷款利息表').then((res) => {
            if (res.data.success === 1) {
              console.log(res.data)
              _this.t1 = res.data.sheet
