            } else { // 清除登录态
              sessionStorage.clear()
              console.log('登录超时')
              _this.$router.push({
                name: 'login'
              })
            }
          })
        } else if (activeName === 'fifth') {
          _this.loading = true
          getsheetHtml('T6-T8 XX风险计量表').then((res) => {
            if (res.data.success === 1) {
              console.log(res.data)
              _this.t1 = res.data.sheet
              _this.loading = false
            } else { // 清除登录态
