            }
          })
        } else if (activeName === 'ninth') {
          _this.loading = true
          getsheetHtml('T12监管表').then((res) => {
            if (res.data.success === 1) {
              console.log(res.data)
              _this.t1 = res.data.sheet
              _this.loading = false
            } else { // 清除登录态
              sessionStorage.clear()
              console.log('登录超时')
              _this.$router.push({
