import axios from 'axios'
// var qs = require('qs')
export function getsheet (t0, t3t4) {
  return axios.post('http://localhost:8080/excel/calculate', {
    't0': t0,
    't3_t4': t3t4
  },
  {
    headers: {
      'Content-Type': 'application/json'
    },
    withCredentials: true
  })
}



