import axios from 'axios'
var qs = require('qs')
export function getsheetHtml (sheet) {
  return axios.post('http://localhost:8080/excel/sheethtml', qs.stringify({
    'sheet': sheet
  }, {indices: false}),
  {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    withCredentials: true})
}



